@tailwind base;
@tailwind components;
@tailwind utilities;
@import 'https://cdnjs.cloudflare.com/ajax/libs/tailwindcss/2.2.19/tailwind.min.css';

.editor-container {
  @apply p-4 border border-gray-300 rounded mt-4 bg-blue;
  min-height: 200px;
  cursor: text;
}
.public-DraftEditor-content span[data-offset-key] {
  @apply bg-yellow-300;
}
.editor-container .public-DraftStyleDefault-pre {
  background-color: #f5f5f5;
  padding: 8px;
  border-radius: 4px;
  overflow-x: auto;
}
